.slider_wrapper{
  position: relative;
  width: 100%;
  min-width: 0;
  padding: 10px 0;
  .slide_wrap{
    width: 100%;
    img{
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 1;
      border-radius: 5px;
      object-fit: cover;
    }
    p{
      position: absolute;
      z-index: 2;
      color: white;
      margin: 15px;
      font-size: 14px;
      font-style: italic;
      opacity: 0.5;
      transition: ease 300ms;
      &:hover{
        opacity: 1;
        cursor:default;
      }
    }
  }
  @media screen and (min-width: 1900px) and (max-width: 4000px){
    .slide_wrap{
      height: 700px
    }
  }
  @media screen and (max-width: 1900px) and (min-width: 1500px){
    .slide_wrap{
      height: 600px;
    }
  }
  @media screen and  (max-width : 1500px) and (min-width: 1200px) {
    .slide_wrap{
      height: 500px;
    }
  }
  @media screen and  (max-width: 1200px) and (min-width: 900px) {
    .slide_wrap{
      height: 400px;
    }
  }
  @media screen and  (max-width: 900px) and (min-width: 700px) {
    .slide_wrap{
      height: 300px;
    }
  }
  @media screen and  (max-width: 700px) and (min-width: 500px) {
    .slide_wrap{
      height: 250px;
    }
  }
  @media screen and  (max-width: 500px) and (min-width: 350px) {
    .slide_wrap{
      height: 200px;
    }
  }
  @media screen and  (max-width: 350px) and (min-width: 0px) {
    .slide_wrap{
      height: 200px;
    }
  }
}